:root {
  --iq-light-primary: rgba(226, 14, 2, 0.2);
  --iq-primary: #e20e02;
  --iq-light-success: rgba(20, 231, 136, 0.2);
  --iq-success: #14e788;
  --iq-light-waring: rgba(246, 138, 40, 0.2);
  --iq-warning: #f68a04;
}


// custom auth css

.sign-in-page {
    height: 100vh;
    position: relative;
    background: url(../../assets/images/login/login.jpg) no-repeat scroll 0 0;
    background-size: cover;
}

//custom card css

.sign-user_card {
    position: relative;
    background: rgba(0, 0, 0, 0.5);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    padding: 25px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    display: block;
    margin: 0 auto;
}

.sign-in-page .height-self-center {
    height: 100vh;
    border-radius: 15px;
}

//utilities custom css

.iq-error {
    position: relative;
    width: 100%;
    overflow: hidden;
    display: inline-block;
}

.iq-error>img {
    width: 50%;
}

.iq-error-img {
    position: static;
    display: inline-block;
}

//dashboard form

.section-form{

fieldset,
legend {
  all: revert ;
  margin-bottom: 1rem;
}
fieldset {
  border: 3px solid rgba(0, 0, 0, 0.1);
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  border-radius: 10px;
}

legend {
  font-size: 1.4rem;
  font-weight: bold;
  padding: 0 0.7rem;
  color: var(--bs-primary);
}

.multiselect-tags-search {
  background-color: inherit;
  color: gray;
}

.multiselect-tag {
  background-color: var(--bs-primary);
}
}

.prevMonthDay{
  color: #808080 !important;
}
.prevMonthDay:hover{
  color: #ffffff !important;
}

.flatpickr-day:hover{
  background: var(--bs-primary) !important;
}

.list-user-action a {
  font-size: 14px;
  height: 30px;
  width: 30px;
  text-align: center;
  line-height: 30px;
  margin: 0 3px;
  display: inline-block;
  border-radius: 5px;
}

.iq-bg-warning {
  background: var(--iq-light-waring) !important;
  color: var(--iq-warning);
}

.iq-bg-success {
  background: var(--iq-light-success) !important;
  color: var(--iq-success);
}

.iq-bg-primary {
  background: var(--iq-light-primary) !important;
  color: var(--iq-primary) !important;
}


//logo

.logo-normal{
  display: none;
  height: 40px;
  min-height: 40px;
  opacity: 1 !important;
  transform: translateX(0);
  transition: all .3s ease-in-out;
}

.logo-full {
  height: 40px;
  opacity: 1;
  transform: translateX(0);
  transition: all .3s ease-in-out;
}

.logo-full-white {
  display: none;
}


.data-tables td p {
  white-space: nowrap;
  width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.iq-banner .iq-navbar-header .iq-header-img {
  border-radius: 0.5rem;
  background-color: var(--bs-primary);
  height: 200px;
  overflow: hidden;
  position: absolute;
  top: 0;
  transform-origin: center left;
  transition: all .4s;
  width: 100%;
  z-index: -1;
}

.dt-row{
  margin: 8px 0 !important;
}

.dark .iq-header-img {
  background-color: #313131 !important;
}

.dark .table-striped>tbody>tr:nth-of-type(even)>* {
  --bs-table-accent-bg: #181818 !important;
}

.bulk-action{
  width: 40% !important;
}

.chevron-right{
  display: none;
}

.sidebar-mini {
  .logo-normal {
    display: block;
  }
  .logo-full {
    display: none;
  }
  .chevron-right{
    display: block;
  }
  .chevron-left{
    display: none;
  }
}


//modal 
.modal-backdrop{
  z-index: -1;
}

// icons

.ri-3x {
  font-size: 3rem !important
}

//swiper-btn
.swiper-button-next {
  right: 0;
}

.swiper-button-prev {
  left: 0;
}

.swiper-button-next,
.swiper-button-prev {
  /* background: #f9b707; */
  border-radius: 50% !important;
  width: 3em !important;
  height: 3em !important;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 1.25em !important;
  color: var(--iq-primary);
  font-weight: 900;
  background: rgba(0, 0, 0, 0.5);
  width: 2.188em;
  height: 3.75em;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 0.3125em;
  box-shadow: 0 0.5625em 1.1875em rgba(1, 4, 27, 0.05);
}

.offcanvas-width-80 {
  width: 80% !important;
}

.table-responsive.table-space .dataTables_wrapper .row .dataTables_length,
.table-responsive.table-space .dataTables_wrapper .row .dataTables_info {
  padding-left: 0;
}

.table-responsive.table-space .dataTables_wrapper .row .dataTables_filter,
.table-responsive.table-space .dataTables_wrapper .row .dataTables_paginate {
  padding-right: 0;
}

div.dataTables_wrapper div.dataTables_filter input {
  height: 45px;
}
div.dataTables_wrapper div.dataTables_length select {
  height: 45px;
}

.dark {
  .section-form{
    fieldset {
      border: 3px solid rgba(255, 255, 255, 0.1);
    }
  }
}

@media (max-width: 767px) {
  .logo-normal,
  .logo-full {
    height: 30px;
  }
}